.Mockup {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: 'Aboreto', cursive;
  margin-bottom: 100px;
}

.image-computer{
  width: 40%;
}

.Mockup-Description h2{
  font-size: 40px;
  margin: 0;
}

.electronics {
  margin-top: 0;
  color: #FFFF;
}

.info{
  text-decoration: none;
  color: black ;
}

.info-livedemo{
  text-decoration: none;
  color: black ;
  padding-left: 25px;
}

.right-description{
  font-size: 20px;
}

.image-walkin{
  width: 13%;
}

